body {
    background-color: black;
    color: limegreen;
    padding: 10px;
}

html {
  scrollbar-width: thin;           
  scrollbar-color: #00ff0d #222; 
}

/* (Chrome, Edge, Safari) */
html::-webkit-scrollbar {
  width: 10px;  
  height: 10px;      
}

html::-webkit-scrollbar-track {
  background: #222;
  border-radius: 5px;
}

html::-webkit-scrollbar-thumb {
  background: #00e613;  
  border-radius: 5px;
  border: 2px solid #222; 
  transition: background 0.3s, transform 0.3s;
}

html::-webkit-scrollbar-thumb:hover {
  background: #16bd00;    
  transform: scale(1.1);   
}

.header {
    padding: 10px;
    font-size: 18pt;
    font-family: "MineFont";
    text-align: center;
    text-decoration-color: aqua;
    border: 2px solid limegreen;
    border-radius: 30px;
    transition: background-color 0.3s ease-in-out;
}

.header:hover {
    border-color: #06b900;
    transition: 0.3s;
}

@font-face {
    font-family: "MineFont";
    src: url("../fonts/minecraft.ttf");
}

.profile-cont {
    display: flex;
    /* background-color: rgba(86, 90, 89, 0.678); */
    margin-top: 25px;
    border-radius: 15px;
    padding: 30px;
    gap: 50px;
    position: relative;
    overflow: hidden;
    border: 2px solid limegreen;
    color: white;
}

#profile-back-overlay {
    position: absolute;
    inset: 0;
    z-index: -1;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: 50% 50%;
    filter: blur(15px);
    opacity: 70%;
    transition: ease-in-out 0.3s;
    align-self: center;
    aspect-ratio: 4 / 3;
}

.profile-cont:hover #profile-back-overlay {
    opacity: 100%;
    filter: blur(10px);
}

.avatar-img {
    width: 256px;
    height: 256px;
    border-radius: 50%;
    border: 3px solid cyan;
    align-self: center;
    transition: ease-in-out 0.3s;
    image-rendering: pixelated;
}

.avatar-img:hover {
    scale: 85%;
}

.profi-info h4 {
    font-size: 30pt;
    font-family: "MineFont";
    transition: ease-in-out 0.3s;
}

.profi-info p {
    font-family: "MineFont";
    font-size: 12pt;
    transition: ease-in-out 0.3s;
    color: rgba(184, 179, 179, 0.808)
}

/* .fav-lp-body {

} */

.fav-lp-div {
    height: 50%;
    border: 2px solid limegreen;
    border-radius: 20px;
    padding: 20px;
    margin-top: 25px;
    align-items: center;
}
.fav-lp-div h3 {
    font-size: 18pt;
    alignment-baseline: central;
    font-family: "MineFont";
    transition: ease-in-out 0.3s;
}

.fav-lp-item {
    height: 120px;
    width: 120px;
    border: 2px solid limegreen;
    border-radius: 20px;
    margin-top: 20px;
    display: grid;
    place-items: center;
    padding: 5px;
    flex: 0 0 auto;
    border: 3px solid limegreen;
    transition: ease-in-out 250ms;
}

.fav-lp-item:hover {
    scale: 80%;
    opacity: 50%;
}


.fav-lp-item p {
    font-family: "MineFont";
}

.fav-lp-list {
    display: flex;
    gap: 15px;
    overflow-y: auto;
    scroll-behavior: smooth;
    padding-bottom: 15px;
}

.fav-lp-item img {
    width: 72px;
    height: 72px;
    object-fit: contain;
    padding: 5px;
}

.fav-frameworks-div {
    height: 50%;
    border: 2px solid limegreen;
    border-radius: 20px;
    padding: 20px;
    margin-top: 25px;
    align-items: center;
}
.fav-frameworks-div h3 {
    font-size: 18pt;
    alignment-baseline: central;
    font-family: "MineFont";
    transition: ease-in-out 0.3s;
}

.fav-frameworks-item {
    height: 120px;
    width: 120px;
    border-radius: 20px;
    margin-top: 20px;
    display: grid;
    place-items: center;
    padding: 0px;
    flex: 0 0 auto;
    border: 3px solid limegreen;
    transition: ease-in-out 250ms;
}

#st-big {
    font-size: 11pt;
}

.fav-frameworks-item:hover {
    scale: 80%;
    opacity: 50%;
}

.fav-frameworks-item p {
    font-family: "MineFont";
}

.fav-frameworks-list {
    display: flex;
    gap: 15px;
    overflow-y: auto;
    scroll-behavior: smooth;
    padding-bottom: 15px;
}

.fav-frameworks-item img {
    width: 72px;
    height: 72px;
    object-fit: contain;
    padding: 5px;
}

.bio-body {
    margin-top: 25px;
}

.bio-div {
    border-radius: 20px;
    border: 2px solid limegreen;
    padding: 10px;
    height: 50%;
}

.bio-div h3 {
    font-family: "MineFont";
    font-size: 18pt;
    alignment-baseline: central;
    transition: ease-in-out 0.3s;
    padding-left: 10px;
}

.bio-info {
    border: 2px solid limegreen;
    margin-top: 20px;
    border-radius: 20px;
    padding: 10px;
    padding-left: 20px;
}

.bio-info p {
    font-family: "MineFont";
    font-size: 15pt;
}

#bio-more-info {
    padding-top: 30px;
}

.friends-body {
    margin-top: 25px;
}

.friends-div {
    padding: 10px;
    border: 2px solid limegreen;
    border-radius: 20px;
    padding-left: 20px;
}
.friends-div h3 {
    font-family: "MineFont";
    font-size: 16pt;
}

.friends-list {
    margin-top: 25px;
    padding-bottom: 15px;
    display: flex;
    gap: 15px;
    overflow-y: scroll;
    scroll-behavior: smooth;
}

.friend-card {
    display: flex;
    border-radius: 15px;
    padding: 15px;
    gap: 15px;
    position: relative;
    overflow: hidden;
    border: 3px solid limegreen;
    width: 300px;
    flex: 0 0 auto;
    align-items: center;
    justify-items: center;
    text-align: center;
    transition: ease-in-out 200ms;
}

.friend-card:hover {
    scale: 80%;
    opacity: 75%;
}

.friend-card-av {
    border-radius: 50%;
    width: 72px;
    height: 72px;
}

#friend-cdata p {
    font-family: "MineFont";
}

.contact-div {
    border: 3px solid limegreen;
    border-radius: 20px;
    padding: 10px;
    padding-left: 20px;
}

.contact-div h3 {
    font-family: "MineFont";
}

.contact-list {
    margin-top: 25px;
    padding-bottom: 15px;
    display: flex;
    gap: 15px;
    overflow-y: scroll;
    scroll-behavior: smooth;
}

.contact-p {
    border: 3px solid limegreen;
    border-radius: 20px;
    padding: 20px;
    align-items: center;
    justify-content: center;
    text-align: center;
    transition: ease-in-out 200ms;

}

.contact-p:hover {
    scale: 85%;
    opacity: 85%;
}

.contact-p-icon {
    width: 96px;
    height: 96px;
}

.contact-p-name {
    font-family: "MineFont";
}

.end-banner {
    text-align: center;
    font-family: "MineFont";
    color: #06b900a9;
    margin-top: 30px;
    animation: endBannerA 3s infinite alternate;
}

@keyframes endBannerA {
    0% {
        color: #06b900;
    }
    100% {
        color: white;
    }
}

@media (max-width: 600px) {
    .profi-info h4 {
        font-size: 18pt;
    }
    .profile-cont {
        gap: 20px;
    }
    .avatar-img {
        width: 148px;
        height: 148px;
    }
    .header {
        font-size: 12pt;
    }
    .fav-lp-div h3 {
        font-size: 14pt;
    }
    .fav-frameworks-div h3 {
        font-size: 12pt;
    }
    .bio-div h3 {
        font-size: 12pt;
    }
    .bio-info p {
        font-size: 9pt;
    }
    .friends-div h3 {
        font-size: 12pt;
    }
    .contact-div h3 {
        font-size: 10pt;
    }
    .end-banner p {
        font-size: 10pt;
    }
}
